import { useEffect } from 'react';
import metro from '../images/metro-port.jpg'
import sama from '../images/sama-port.jpg'
import lala from '../images/lala-port.jpg'
import kumho from '../images/kumho-port.jpg'
import todo from '../images/todo-cover.jpg'
import { Viewer, Worker } from '@react-pdf-viewer/core';

//metro
function Popup(props){
    return(
        <div className='popupbg'>
            <div className="popup">
                <div className="head">
                    <h4>{props.modal}</h4>
                    <SetLink modal={props.modal}></SetLink>
                    <button className="close" onClick={()=>{
                        props.setModal(false);
                    }}>
                        <span></span>
                        <span></span>
                    </button>
                </div>
                <div className="body">
                    <SetImage modal={props.modal}></SetImage>
                </div>
            </div>
        </div>
    )
}

function SetImage(props){
    if (props.modal == '') {
        return <img src={metro} alt=''></img>
    } else if (props.modal == '') {
        return <img src={sama} alt=''></img>
    } else if (props.modal == '  ') {
        return <img src={lala} alt=''></img>
    } else if (props.modal == '') {
        return <img src={kumho} alt=''></img>
    } else if (props.modal == 'Todo App') {
        return <img src={todo} alt=''></img>
    }
}

function SetLink(props) {

    if (props.modal == '') {
        return <div className='link'>
            <a target='_blank' href='https://metrohospital.netlify.app/'> </a>
            <a target='_blank' href='https://metrohospital.netlify.app/sub-review'>  </a>
            <a target='_blank' href='https://metrohospital.netlify.app/sub-sur'>  </a>
            </div>
    } else if (props.modal == '') {
        return <div className='link'><a target='_blank' href='https://samaerection.netlify.app/'> </a></div>
    } else if (props.modal == '  ') {
        return <div className='link'><a target='_blank' href='http://kimwoojin083.dothome.co.kr/lalasupia'> </a></div>
    } else if (props.modal == '') {
        return <div className='link'><a target='_blank' href='https://kumhotire.netlify.app/'> </a></div>
    } else if (props.modal == 'Todo App') {
        return <div className='link'><a target='_blank' href='https://reacttodoapp111.netlify.app/'> </a></div>
    }
}


export default Popup;