import { useState } from "react";
import Con from "./content";
import Popup from "./popup";
import PopupMetro from "./popup";

function Web(props){
    let [content, setContent] = useState(
        [{
            name: '',
            project: '',
            page: ',  5',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '',
            project: '',
            page: ',  2',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: ' ',
            project: '',
            page: ',  6',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '',
            project: '',
            page: ',  ',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '  ',
            project: '',
            page: ',  2, 3D 14',
            percent: '3D[(100%), (100%), (50%)]',
            tool: 'Figma, Photoshop, Illustrator, VScode, 3Dmax'
        }, {
            name: 'Todo App',
            project: '',
            page: '',
            percent: '100%',
            tool: 'Figma, VScode'
        }]
    )
    let [modal, setModal] = useState(false);

    return(
        <div className="web">
            <div className="container">
                <h2>Web-Project</h2>
                <div className="wrap">
                    {
                        content.map((b, i)=>{
                            return(
                                <Con b={b} i={i} content={content} key={i} modal={modal} setModal={setModal}></Con>
                            )
                        })
                    }
                </div>
                {
                    modal == '' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '  ' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == 'Todo App' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
            </div>
        </div>
    )
}


export default Web;